<template>
	<van-empty v-if="showEmpty" class="custom-image" :image="image">
		<div class="historyEmptyTip">
			<div class="tips" v-if="emptyTip">{{$t(emptyTip)}}</div>
			<template v-if="type == 1">
				<div class="tipsBtn" @click="emptyBtnClick" v-if="btnText">
					<div class="go">{{$t(btnText)}}</div>
					<div class="tipsImg">
						<img src="@/assets/go.png">
					</div>
				</div>
			</template>
		</div>
	</van-empty>
</template>

<script>
	export default {
		name: 'empty',
		props: {
			btnText: {
				type: String
			},
			showEmpty: {
				type: Boolean,
				default: false
			},
			image: {
				type: String
			},
			emptyTip: {
				type: String
			},
			type: {
				type: Number,
				default: 1
			}
		},
		methods: {
			emptyBtnClick() {
				this.$emit('emptyBtnClick')
			}
		}
	}
</script>

<style scoped lang="less">
	.historyEmptyTip {
		display: flex;
		flex-direction: column;
		justify-content: center;

		.tips {
			margin-top: 14px;
			font-size: 14px;
			font-weight: 400;
			color: #909CBA;
		}

		.tipsBtn {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: center;
			margin-top: 22px;

			.go {
				font-size: 14px;
				font-weight: 500;
				color: #1F75D9;
			}

			.tipsImg {
				margin-left: 10px;
				width: 7px;
				height: 13px;
			}
		}
	}

	.custom-image /deep/.van-empty__image {
		margin-top: 104px;
		width: 118px;
		height: 89px;
	}
</style>
